<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="currentValue">
      <c-header isBackNav>
        <p class="c-header-title" slot="middle">{{$t('base.ReasonForNotSelectingTheLowestPrice')}}</p>
      </c-header>
      <hs-scroll class="table-view" :data="details.ReasonCodes">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-header">{{$t('base.AccordingToYourCompanysTravelPolicy')}}</div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1
              v-for="(item, index) in details.ReasonCodes" :key="index"
              :value="item.ReasonDescription"
              logoName="select.png"
              :showLogo="selectedIndex === index"
              @click.native="selectedIndex = index"></hs-cell-1>
            <hs-cell-1 v-if="details.IsDisplayOtherItem || !details.ReasonCodes || details.ReasonCodes.length === 0" logoName="select.png" :showLogo="selectedIndex === undefined" @click.native="selectedIndex = undefined">
              <div class="other-reason" :style="{width: selectedIndex === undefined ? 'calc(100% - 30px)' : '100%'}">
                <textarea v-model="otherReason" :placeholder="$t('base.OtherReasons')"></textarea>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <hs-gradient-button :title="$t('base.ContinueBooking')" @click="_gotoDetails"></hs-gradient-button>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 选择超标原因页面
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    otherReason(val) {
      !this.noValue(val) && (this.selectedIndex = undefined)
    },
    currentValue(val) {
      if (val) {
        this.selectedIndex = -1
        this.otherReason = ''
      }
    }
  },
  data() {
    return {
      selectedIndex: -1,
      otherReason: ''
    }
  },
  methods: {
    _gotoDetails() {
      if ((this.noValue(this.otherReason.trim()) && (this.selectedIndex === undefined)) || this.selectedIndex === -1) {
        this.$toast(this.$t('TM.PleaseSelectOrFillInTheReasonForExceedingTheStandard'))
        return
      } else if (this.otherReason.length > 50) {
        this.$toast(this.$t('TM.TheReasonForExceedingTheStandardCannotExceedXWords'))
        return
      }
      let ViolationFloorPirceReason = this.selectedIndex === undefined ? `${this.$t('base.Other')}: ${this.otherReason}` : this.details.ReasonCodes[this.selectedIndex].ReasonDescription
      this.currentValue = false
      this.$emit('delegate', ViolationFloorPirceReason)
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsGradientButton,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  background-color $color_white
  z-index 200
  .c-header-title[type="EN"]
    font-size 0.6rem
    line-height 1.3em
  .table-view
    width 100%
    height calc(100% - 45px - 100px)
    overflow hidden
    .custom-cell-header
      width 100%
      padding 10px 0
      line-height 1.2em
    .other-reason
      width 100%
      padding 15px 0
      >textarea
        width 100%
        height 80px
        background-color $color_white
        font-size 1rem
        padding 10px
        border 1px solid $color_gray_layer
        border-radius 2px
        resize none
        outline none
        box-shadow 0px 0px 0px rgba(0, 0, 0, 0)
        -webkit-appearance none
</style>



