<template>
  <transition name="slide">
    <div class="hs-container" v-if="currentShow">

      <c-header isBackNav>
        <div slot="middle" class="header-title">
          <h2 v-if="isGoFlight || isOneWay">
            {{flightOrder.DepartObject ? flightOrder.DepartObject[`AirPortName_${language}`] : flightOrder.DepartCityName}}-{{flightOrder.ArrivalObject ? flightOrder.ArrivalObject[`AirPortName_${language}`] : flightOrder.ArrivalCityName}}
            <template>({{Number(flightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</template>
          </h2>
          <h2 v-else>
            {{flightOrder.ArrivalObject ? flightOrder.ArrivalObject[`AirPortName_${language}`] : flightOrder.ArrivalCityName}}-{{flightOrder.DepartObject ? flightOrder.DepartObject[`AirPortName_${language}`] : flightOrder.DepartCityName}}
            <template>({{Number(flightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</template>
          </h2>
          <!-- <h2 v-if="language === 'EN'">({{Number(flightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</h2> -->
        </div>
      </c-header>

      <hs-scroll
        ref="tableView"
        class="table-view"
        :data="[details]">
        <ul>
          <hs-section>
            <div class="cell">
              <div class="travel-details">
                <div class="travel">
                  <div class="source">
                    <p>{{$t(beTime(details.DepartDateTime), 'date', ['MM-dd', 'MM dd'])}} {{getWeeks(beTime(details.DepartDateTime))}}</p>
                    <p>{{beTime(details.DepartDateTime, 'hh:mm')}}</p>
                    <p>{{details.DepartAirportAbbrName}} {{details.DepartTerminal}}</p>
                  </div>
                  <div class="middle">
                    <div class="stopOver-logo" v-if="details.StopOver">
                      <p>{{$t('base.By')}}</p>
                    </div>
                    <div class="logo" v-else style="background-image: url('static/image/home/trip_plane.png')"></div>
                    <div class="dashed"></div>
                    <p class="stopOver" v-if="details.StopOver">{{details.StopOver.StopCity}}</p>
                  </div>
                  <div class="destination">
                    <p>{{$t(beTime(details.ArrivalDateTime), 'date', ['MM-dd', 'MM dd'])}} {{getWeeks(beTime(details.ArrivalDateTime))}}</p>
                    <p>{{beTime(details.ArrivalDateTime, 'hh:mm')}}<span v-if="details.AddDays > 0">+{{details.AddDays}}</span></p>
                    <p>{{details.ArrivalAirportAbbrName}} {{details.ArrivalTerminal}}</p>
                  </div>
                </div>
                <div class="company">
                  <div class="logo" :style="{'background-image': `url(${details.AirLineLogoUrl})`}"></div>
                  <p>
                    {{details.AirLineName}}&nbsp;<span v-if="details.IsCodeShare">{{$t('base.CodeShare')}}</span>&nbsp;{{details.FlightNo}}<template v-if="details.PlaneName">&nbsp;|&nbsp;{{details.PlaneName}}</template><template v-if="details.PlaneType">&nbsp;{{details.PlaneType}}</template>&nbsp;|&nbsp;{{details.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}
                    <template v-if="details.IsCodeShare">
                      <br />
                      <p>{{$t('base.OperatedBy')}}&nbsp;{{details.ActualCarryAirLineName}}&nbsp;{{details.ActualCarryFlightNo}}</p>
                    </template>
                  </p>
                </div>
              </div>
            </div>
          </hs-section>

          <hs-section v-if="!noValue(JSON.stringify(tapOptions))">
            <hs-tab :options="tapOptions" @select="_getShowCabins"></hs-tab>
          </hs-section>

          <div style="width: 100%; height: 10px;"></div>
          <div v-for="(item, index) in showCabins" :key="index"
            class="cabin">
            <div class="details">
              <div class="header">
                <div class="price">
                  <span>￥</span>
                  <p>{{item.SalePrice}}</p>
                </div>
                <div v-if="item.PolicyInfo.EcoPrice.Adult > 0" class="original">
                  <p>￥{{item.OriginPrice}}</p>
                  <p>{{$t('base.Save_2')}}￥{{item.PolicyInfo.EcoPrice.Adult}}</p>
                </div>
              </div>
              <div class="others">
                <div class="canbin-type">{{item.CabinName}}</div>
                <div @click="_getFlightCabinRule(item)"
                  class="rc">{{$t('base.TicketRestrictions')}} ></div>
              </div>
              <div class="limit" v-if="!noValue(JSON.stringify(item.LimitCredentialTypes))">
                <p>{{_getLimitCredentialTypes(item.LimitCredentialTypes)}}</p>
              </div>
              <div v-if="item.FeatureOfProduct && item.ExternalProductDescription" @click="_openFeatureOfProduct(item)"
                class="featureOfProduct">
                <div class="item">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/infor-list/brand.png')"></div>
                  <p>{{item.FeatureOfProduct}}</p>
                </div>
              </div>
            </div>
            <div class="right">
              <hs-button @click="$emit('clickBook', details, item)"
                type="gradient" height="42px">{{$t('base.Book')}}</hs-button>
              <div v-if="item.TicketsCount < 10"
                class="TicketsCount"
                :type="language">{{item.TicketsCount}}{{$t('base.Tickets')}}</div>
            </div>

            <!-- 协议价描述 -->
            <p v-if="item.ClientCabinPriceTypeDesc" class="typeDesc">{{item.ClientCabinPriceTypeDesc}}</p>
          </div>
          <div style="width: 100%; height: 1px;"></div>
        </ul>
      </hs-scroll>

      <!-- 产品特色 -->
      <transition name="opacity">
        <div v-if="showFeatureOfProduct" @click.stop="showFeatureOfProduct = false"
          class="externalProductDescription">
          <hs-scroll class="table-view" :data="[featureOfProduct]">
            <ul>
              <div class="details" v-if="details">
                <div class="header">{{featureOfProduct.title}}</div>
                <div class="item">
                  <p v-html="featureOfProduct.des"></p>
                </div>
              </div>
            </ul>
          </hs-scroll>
        </div>
      </transition>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsTab from 'base/hs-tab/hs-tab'
import {mapGetters, mapMutations} from 'vuex'
import {kpiFlightGetCabins, kpiFlightGetFlightCabinRule} from 'api'
// import {addClass, removeClass} from 'common/js/dom'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    },
    isOneWay: {
      type: Boolean
    },
    isGoFlight: {
      type: Boolean
    },
    flightFilters: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data() {
    return {
      /** 获取到的舱位列表 */
      datalist: [],
      /** 根据tab展示的舱位列表 */
      showCabins: [],
      /** tab选项 */
      tapOptions: [],
      /** tab选中 */
      selectedIndex: 0,
      /** 产品特色 */
      showFeatureOfProduct: false,
      featureOfProduct: {
        title: '',
        des: ''
      }
    }
  },
  computed: {
    ...mapGetters([
      'flightOrder'
    ]),
    currentShow: {
      get: function(val) {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    },
    // 是否为改期订单
    IsChangeOrder() {
      return !this.noValue(this.flightOrder.OriginPnrID)
    }
  },
  watch: {
    currentShow(val) {
      if (val) {
        this._getCabins()
      }
    }
  },
  methods: {
    ...mapMutations([
      'SET_FLIGHT_ORDER'
    ]),
    _getCabins() {
      this.datalist = []
      this.showCabins = []
      this.tapOptions = []
      let CabinFilter = this.flightFilters.filter(v => v.FilterType === 6)[0]
      let params = {
        DepartCode: (this.isGoFlight || this.isOneWay) ? this.flightOrder.DepartCode : this.flightOrder.ArrivalCode,
        ArrivalCode: (this.isGoFlight || this.isOneWay) ? this.flightOrder.ArrivalCode : this.flightOrder.DepartCode,
        FlightNo: this.details.FlightNo,
        DepartDate: this.details.DepartDateTime,
        CabinFilter: {
          FilterType: CabinFilter.FilterType,
          SelectedValues: CabinFilter.Items.filter(a => a.IsChecked).map(b => b.ItemValue)
        },
        IsChangeOrder: this.IsChangeOrder
      }
      kpiFlightGetCabins(params).then((res) => {
        console.log('舱位列表: ', res)
        this.datalist = res.ResultData

        // 设置tapOptions
        let CabinTypes = this.datalist.map(v => v.CabinType)
        if (((CabinTypes.indexOf('经济舱') >= 0) || (CabinTypes.indexOf('超级经济舱') >= 0)) && (CabinTypes.indexOf('公务舱') >= 0) && (CabinTypes.indexOf('头等舱') >= 0)) {
          this.tapOptions = [this.$t('base.All'), this.$t('base.FirstBusinessClass'), this.$t('base.EconomyClass')]
        } else if (((CabinTypes.indexOf('经济舱') >= 0) || (CabinTypes.indexOf('超级经济舱') >= 0)) && (CabinTypes.indexOf('公务舱') >= 0)) {
          this.tapOptions = [this.$t('base.All'), this.$t('base.BusinessClass'), this.$t('base.EconomyClass')]
        } else if (((CabinTypes.indexOf('经济舱') >= 0) || (CabinTypes.indexOf('超级经济舱') >= 0)) && (CabinTypes.indexOf('头等舱') >= 0)) {
          this.tapOptions = [this.$t('base.All'), this.$t('base.FirstClass'), this.$t('base.EconomyClass')]
        }

        // 设置showCabins
        this._getShowCabins(0)
      })
    },
    // 筛选出需要展示的舱位
    _getShowCabins(selectedIndex) {
      this.selectedIndex = selectedIndex
      if (this.selectedIndex === 0) {
        this.showCabins = this.datalist
      } else if (this.selectedIndex === 1) {
        this.showCabins = this.datalist.filter(v => v.CabinType === '头等舱' || v.CabinType === '公务舱')
      } else if (this.selectedIndex === 2) {
        this.showCabins = this.datalist.filter(v => v.CabinType === '经济舱' || v.CabinType === '超级经济舱')
      }
    },
    _getFlightCabinRule(item) {
      let params = {
        DepartCode: (this.isGoFlight || this.isOneWay) ? this.flightOrder.DepartCityCode : this.flightOrder.ArrivalCityCode,
        ArrivalCode: (this.isGoFlight || this.isOneWay) ? this.flightOrder.ArrivalCityCode : this.flightOrder.DepartCityCode,
        FlightNo: this.details.FlightNo,
        CabinID: item.CabinID,
        DepartDate: this.details.DepartDateTime,
        IsChangeOrder: this.IsChangeOrder
      }
      console.log('退改签params： ', params)
      kpiFlightGetFlightCabinRule(params).then((res) => {
        console.log('退改签政策： ', res)
        this.$emit('clickReturnExplain', res.ResultData)
      })
    },
    // 受限证件类型由数组转为字符串展示
    _getLimitCredentialTypes(types) {
      let typeDetails = types.map(v => {
        switch (v) {
          case 1:
            return this.$t('base.IDTypeNames.IDCard')
          case 2:
            return this.$t('base.IDTypeNames.Passport')
          case 3:
            return this.$t('base.IDTypeNames.StudentCard')
          case 4:
            return this.$t('base.IDTypeNames.MilitaryID')
          case 5:
            return this.$t('base.IDTypeNames.HomeReturnPermit')
          case 6:
            return this.$t('base.IDTypeNames.ForeignersPermanentResidencePermit')
          case 7:
            return this.$t('base.IDTypeNames.EntryExitPassForHongKongAndMacau')
          case 8:
            return this.$t('base.IDTypeNames.EntryExitPassForTaiwan')
          case 9:
            return this.$t('base.IDTypeNames.InternationalSeafarersCard')
          case 10:
            return this.$t('base.IDTypeNames.TaiwanCompatriotPermit')
          case 11:
            return this.$t('base.IDTypeNames.Others')
          case 12:
            return this.$t('base.IDTypeNames.HongKongMacaoAndTaiwanResidentsResidencePermit')
        }
      })
      return this.$t('TM.LimitedToXReservation', 'function', typeDetails.join('、'))
    },

    /** 打开产品特色 */
    _openFeatureOfProduct(item) {
      this.showFeatureOfProduct = true
      this.featureOfProduct = {
        title: item.FeatureOfProduct,
        des: item.ExternalProductDescription
      }
    }
  },
  components: {
    CHeader,
    HsButton,
    HsCell1,
    HsScroll,
    HsSection,
    HsTab
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.hs-container
  @extend .flex-column
  @extend .flex-start
  width 100%
  height 100%
  position absolute
  top 0
  left 0
  background-color $color_gray_background_0
  z-index 10
  .header-title
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 100%
    >h2
      font-size 1.1rem
      font-weight bold
  .header-title[type="EN"]
    >h2
      font-size 0.8rem

  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .cell
      width 100%
      padding 0 25px
      >.travel-details
        width 100%
        padding 15px 0
        >.travel
          @extend .flex-row
          @extend .flex-between
          width 100%
          >.source
            @extend .flex-column
            @extend .flex-center
            width 110px
            >p
              &:nth-child(1)
                font-size 0.8rem
                line-height 1.4em
                color $color_black_text_0
              &:nth-child(2)
                font-size 1.8rem
                font-weight bold
                line-height 1.2em
              &:nth-child(3)
                font-size 0.8rem
                line-height 1.4em
          >.middle
            @extend .flex-center
            width calc(100% - 230px)
            position relative
            .dashed
              position absolute
              width 100%
              height 0px
              border-top 1px dashed $color_black_text_0
              top 50%
              left 0
            .logo
              width 15px
              height 15px
              background-size 100% 100%
              background-color $color_white
            >.stopOver
              position absolute
              top 18px
              left 0
              font-size 0.6rem
              color $color_theme
              width 100%
              text-align center
            >.stopOver-logo
              @extend .flex-center
              width 24px
              height 18px
              background-color $color_white
              z-index 9
              >p
                text-align center
                width 18px
                line-height 1.2em
                color $color_theme
                font-size 0.6rem
          >.destination
            @extend .flex-column
            @extend .flex-center
            width 110px
            >p
              &:nth-child(1)
                font-size 0.8rem
                line-height 1.4em
                color $color_black_text_0
              &:nth-child(2)
                font-size 1.8rem
                font-weight bold
                line-height 1.2em
                position relative
                >span
                  color $color_theme
                  font-size 0.8rem
                  position absolute
                  top -4px
                  right -16px
              &:nth-child(3)
                font-size 0.8rem
                line-height 1.4em
                text-align right
        >.company
          @extend .flex-row
          @extend .flex-start
          @extend .flex-top
          width 100%
          font-size 0.7rem
          color $color_black_text_0
          margin-top 5px
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin-right 3px
          >p
            color $color_gray_text
            line-height 1.3em
            width calc(100% - 15px - 3px)
            >span
              color $color_theme

    .cabin
      @extend .flex-row
      @extend .flex-between
      width calc(100% - 20px)
      padding 25px 15px
      margin-bottom 10px
      margin-left 10px
      background-color $color_white
      border-radius 5px
      box-shadow 0 0 10px $color_gray_boxshadow
      position relative
      overflow hidden

      >.details
        width calc(100% - 60px)
        >.header
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          margin-bottom 5px
          >.price
            @extend .flex-row
            @extend .flex-start
            margin-right 5px
            >p
              color $color_theme
              font-size 1.5rem
              font-weight bold
            >span
              color $color_theme
              font-size 0.8rem
              font-weight bold
          >.original
            @extend .flex-row
            @extend .flex-start
            margin-right 5px
            >p
              position relative
              top 2px
              &:nth-child(1)
                color $color_gray_text
                font-size 0.8rem
                text-decoration line-through
                margin-right 5px
              &:nth-child(2)
                color $color_blue_text
                font-size 0.8rem
                font-weight bold
          >.minPrice
            @extend .flex-center
            font-size 0.6rem
            background-color $color_green_layer
            color $color_white
            padding 2px
        >.others
          @extend .flex-row
          @extend .flex-start
          width 100%
          flex-wrap wrap
          >.canbin-type
            color $color_gray_text
            font-size 0.8rem
            line-height 1.2em
            margin-right 10px
          >.rc
            color $color_theme
            font-size 0.8rem
            line-height 1.2em
        >.limit
          @extend .flex-row
          @extend .flex-start
          width 100%
          margin-top 5px
          >p
            color $color_orange_text
            font-size 0.8rem
            line-height 1.2em
        >.featureOfProduct
          @extend .flex-row
          @extend .flex-start
          width 100%
          margin-top 10px
          >.item
            @extend .flex-row
            @extend .flex-start
            height 20px
            >.logo
              width 15px
              height 15px
              background-size 100% 100%
              margin-right 2px
            >p
              font-size 0.8rem
              color $color_orange_text

      >.right
        position relative
        >.TicketsCount
          @extend .flex-center
          @extend .no-wrap
          width 30px
          padding 2px 0
          position absolute
          border 1px solid $color_white
          background-color $color_orange_background_1
          font-size 0.6rem
          color $color_white
          border-radius 2px
          top -8px
          left -15px
          pointer-events none
        >.TicketsCount[type="EN"]
          width 64px
          left -32px

      >.typeDesc
        position absolute
        top 0
        left 0
        background-color $color_orange_text
        color $color_white
        font-size 0.7rem
        padding 5px 8px
        border-radius 0 0 5px 0

  // 产品特色
  .externalProductDescription
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    background-color $color_white_mask
    color $color_black_text_0
    z-index 99
    padding 50px 20px
    >.table-view
      width 100%
      height 100%
      overflow hidden
      .details
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-start
          width 100%
          padding 10px 0
          margin-bottom 10px
          font-size 1.1rem
          font-weight bold
          border-bottom 1px solid $color_black_text_0
        >.item
          width 100%
          margin-bottom 20px
          >h2
            font-size 1.1rem
            font-weight bold
            margin-bottom 5px
          >p
            font-size 0.9rem
            line-height 1.2em
            margin-bottom 5px
          &:last-child
            margin-bottom 10px
</style>



