<template>
  <div class="hs-container">
    <c-header isBackNav>
      <div slot="middle" class="header-title">
        <h2 v-if="isGoFlight || isOneWay">
          {{flightOrder.DepartObject ? flightOrder.DepartObject[`AirPortName_${language}`] : flightOrder.DepartCityName}}-{{flightOrder.ArrivalObject ? flightOrder.ArrivalObject[`AirPortName_${language}`] : flightOrder.ArrivalCityName}}
          <template>({{Number(flightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</template>
        </h2>
        <h2 v-else>
          {{flightOrder.ArrivalObject ? flightOrder.ArrivalObject[`AirPortName_${language}`] : flightOrder.ArrivalCityName}}-{{flightOrder.DepartObject ? flightOrder.DepartObject[`AirPortName_${language}`] : flightOrder.DepartCityName}}
          <template>({{Number(flightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</template>
        </h2>
        <!-- <h2 v-if="language === 'EN'">({{Number(flightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</h2> -->
        <p v-if="details.TotalCount">{{$t('base.FlightsInTotal_1')}}{{details.TotalCount}}{{$t('base.FlightsInTotal_2')}}</p>
      </div>
      <p slot="right" class="standard" v-if="Number(flightOrder.TravelType) === 0" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>
    </c-header>

    <!-- 已选去程 -->
    <div ref="previous-plane-fixed" class="previous-plane-fixed" v-if="!isOneWay && !isGoFlight" @click="showPreviousPlaneDialog = true">
      <div class="details">
        <p>{{$t('base.Going')}}</p>
        <p>
          <!-- {{beTime(flightOrder.GoSelectedFlight.FlightDetails.DepartDateTime, 'hh:mm')}}&nbsp;-
          {{beTime(flightOrder.GoSelectedFlight.FlightDetails.ArrivalDateTime, 'hh:mm')}}<span v-if="flightOrder.GoSelectedFlight.FlightDetails.AddDays > 0">+{{flightOrder.GoSelectedFlight.FlightDetails.AddDays}}</span>&nbsp; -->
          <template v-if="flightOrder.GoSelectedFlight.FlightDetails.AirLineName">{{flightOrder.GoSelectedFlight.FlightDetails.AirLineName}}</template>
          <span v-if="flightOrder.GoSelectedFlight.FlightDetails.IsCodeShare">&nbsp;{{$t('base.CodeShare')}}</span>
          &nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.FlightNo}}
          <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneName">
            &nbsp;|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.PlaneName}}
          </template>
          <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneType">
            {{flightOrder.GoSelectedFlight.FlightDetails.PlaneType}}
          </template>
          &nbsp;|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}
          &nbsp;{{flightOrder.GoSelectedFlight.DepartDate}}({{getWeeks(flightOrder.GoSelectedFlight.DepartDate)}})
        </p>
      </div>
      <div class="logo" style="background-image: url('static/image/plane-ticket/book/arrow_white.png')"></div>
    </div>

    <!-- 航班列表 -->
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="datalist"
      :options="tableViewOptions"
      @pulling-down="_getDatalistFirst"
      @pulling-up="_getDatalistMore"
      :isListenScroll="true"
      @touchEnd="_touchEnd"
      @scroll="_scroll"
      @scrollStart="_scrollStart">
      <ul>
        <hs-section v-if="!isOneWay && !isGoFlight">
          <div class="previous-plane" @click="showPreviousPlaneDialog = true">
            <div class="details">
              <p>{{$t('base.Going')}}</p>
              <p>
                <!-- {{beTime(flightOrder.GoSelectedFlight.FlightDetails.DepartDateTime, 'hh:mm')}}&nbsp;-
                {{beTime(flightOrder.GoSelectedFlight.FlightDetails.ArrivalDateTime, 'hh:mm')}}<span v-if="flightOrder.GoSelectedFlight.FlightDetails.AddDays > 0">+{{flightOrder.GoSelectedFlight.FlightDetails.AddDays}}</span>&nbsp; -->
                <template v-if="flightOrder.GoSelectedFlight.FlightDetails.AirLineName">{{flightOrder.GoSelectedFlight.FlightDetails.AirLineName}}</template>
                <span v-if="flightOrder.GoSelectedFlight.FlightDetails.IsCodeShare">&nbsp;{{$t('base.CodeShare')}}</span>
                &nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.FlightNo}}
                <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneName">
                  &nbsp;|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.PlaneName}}
                </template>
                <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneType">
                  {{flightOrder.GoSelectedFlight.FlightDetails.PlaneType}}
                </template>
                &nbsp;|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}
                &nbsp;{{flightOrder.GoSelectedFlight.DepartDate}}({{getWeeks(flightOrder.GoSelectedFlight.DepartDate)}})
              </p>
            </div>
            <div class="logo" style="background-image: url('static/image/plane-ticket/book/arrow_white.png')"></div>
          </div>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <c-date-picker
              ref="cDatePicker"
              :date="`${(isGoFlight || isOneWay) ? flightOrder.GoSelectedFlight.DepartDate : flightOrder.BackSelectedFlight.DepartDate}`"
              :overdueDate="`${(isGoFlight || isOneWay) ? beTime(new Date()) : flightOrder.GoSelectedFlight.DepartDate}`"
              @changeDate="_setDate"
              @moreFn="showDatePickerDetails = true"></c-date-picker>
          </hs-cell-1>
        </hs-section>

        <hs-null v-if="noValue(JSON.stringify(datalist))"></hs-null>
        <hs-section v-else
          backgroundColor="#F6F7F8">
          <infor-list-cell
            v-model="datalist[index]"
            v-for="(item, index) in datalist" :key="index"
            @delegateForExtend="_checkDepartTime"></infor-list-cell>
        </hs-section>

        <div style="width: 100%;height: 50px;" v-if="noValue(flightOrder.OriginPnrID)"></div>
      </ul>
    </hs-scroll>

    <div class="footer" ref="footer" :type="language">
      <div class="item" @click="[Sort = Sort === 0 ? 1 : 0, _getDatalist()]">
        <div v-show="Sort !== 0 && Sort !== 1"
          class="logo" style="background-image: url('static/image/plane-ticket/book/time.png')"></div>
        <div v-show="Sort === 0"
          class="logo" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div v-show="Sort === 1"
          class="logo" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p v-if="Sort === 0">{{$t('base.DepartureEarliest')}}</p>
        <p v-else-if="Sort === 1">{{$t('base.DepartureLatest')}}</p>
        <p v-else>{{$t('base.Departure')}}</p>
      </div>
      <div class="item" @click="[Sort = Sort === 4 ? 5 : 4, _getDatalist()]">
        <div v-show="Sort !== 4 && Sort !== 5"
          class="logo" style="background-image: url('static/image/plane-ticket/book/price.png')"></div>
        <div v-show="Sort === 4"
          class="logo" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div v-show="Sort === 5"
          class="logo" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p v-if="Sort === 4">{{$t('base.PriceLowest')}}</p>
        <p v-else-if="Sort === 5">{{$t('base.PriceHighest')}}</p>
        <p v-else>{{$t('base.Price')}}</p>
      </div>
      <div class="item" @click="showFilter = true">
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/screen.png')">
          <div class="dot" v-if="hasFilters"></div>
        </div>
        <p>{{$t('base.Filters')}}</p>
      </div>
    </div>

    <!-- 筛选 -->
    <hs-filter-plane :show.sync="showFilter" :details="details.FlightFilters" @setDetails="_setFlightFilters"></hs-filter-plane>

    <!-- 日期选择器 -->
    <hs-date-picker
      v-model="showDatePickerDetails"
      :title="$t('base.Title')"
      :overdueDate="`${(isGoFlight || isOneWay) ? beTime(new Date()) : flightOrder.GoSelectedFlight.DepartDate}`"
      :date="`${(isGoFlight || isOneWay) ? flightOrder.GoSelectedFlight.DepartDate : flightOrder.BackSelectedFlight.DepartDate}`"
      @changeDate="_changeDateMore"></hs-date-picker>

    <!-- 最低价推荐 -->
    <hs-dialog v-model="showRecommendDialog" ref="recommendDialog" v-if="!noValue(JSON.stringify(cheapestCabinsListRecommend))">
      <div class="recommendDialog" :type="language" @click.stop>
        <div class="card">
          <div class="header">{{$t('base.TheLowestLogicalFareIsSuggestedAccordingToTheTravelPolicyOfYourCompany')}}</div>
          <div class="point1"></div>
          <div class="point2"></div>
          <div class="details">
            <div>
              <div class="travel">
                <div class="source">
                  <p>{{beTime(cheapestCabinsListRecommend.FlightDetails.DepartDateTime, 'hh:mm')}}</p>
                  <p>{{cheapestCabinsListRecommend.FlightDetails.DepartAirportAbbrName}} {{cheapestCabinsListRecommend.FlightDetails.DepartTerminal}}</p>
                </div>
                <div class="middle">
                  <div class="stopOver-logo" v-if="cheapestCabinsListRecommend.FlightDetails.StopOver">
                    <p>{{$t('base.By')}}</p>
                  </div>
                  <div class="logo" v-else style="background-image: url('static/image/home/trip_plane.png')"></div>
                  <div class="dashed"></div>
                  <p class="stopOver" v-if="cheapestCabinsListRecommend.FlightDetails.StopOver">{{cheapestCabinsListRecommend.FlightDetails.StopOver.StopCity}}</p>
                </div>
                <div class="destination">
                  <p>{{beTime(cheapestCabinsListRecommend.FlightDetails.ArrivalDateTime, 'hh:mm')}}<span v-if="cheapestCabinsListRecommend.FlightDetails.AddDays > 0">+{{cheapestCabinsListRecommend.FlightDetails.AddDays}}</span></p>
                  <p>{{cheapestCabinsListRecommend.FlightDetails.ArrivalAirportAbbrName}} {{cheapestCabinsListRecommend.FlightDetails.ArrivalTerminal}}</p>
                </div>
              </div>
              <p>￥{{cheapestCabinsListRecommend.CabinDetails.SalePrice}}</p>
            </div>
            <div>
              <div class="company" :type="language">
                <div class="logo" :style="{'background-image': `url(${cheapestCabinsListRecommend.FlightDetails.AirLineLogoUrl})`}"></div>
                <p>
                  {{cheapestCabinsListRecommend.FlightDetails.AirLineName}}&nbsp;<span v-if="cheapestCabinsListRecommend.FlightDetails.IsCodeShare">{{$t('base.CodeShare')}}</span>&nbsp;{{cheapestCabinsListRecommend.FlightDetails.FlightNo}}<template v-if="cheapestCabinsListRecommend.FlightDetails.PlaneName">&nbsp;|&nbsp;{{cheapestCabinsListRecommend.FlightDetails.PlaneName}}</template><template v-if="cheapestCabinsListRecommend.FlightDetails.PlaneType">&nbsp;{{cheapestCabinsListRecommend.FlightDetails.PlaneType}}&nbsp;|&nbsp;{{cheapestCabinsListRecommend.FlightDetails.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}</template>
                  <template v-if="cheapestCabinsListRecommend.FlightDetails.IsCodeShare">
                    <br />
                    <p>{{$t('base.OperatedBy')}}&nbsp;{{cheapestCabinsListRecommend.FlightDetails.ActualCarryAirLineName}}&nbsp;{{cheapestCabinsListRecommend.FlightDetails.ActualCarryFlightNo}}</p>
                  </template>
                </p>
              </div>
              <p v-if="language === 'CN'" @click="_getFlightCabinRule(cheapestCabinsListRecommend.FlightDetails, cheapestCabinsListRecommend.CabinDetails)">{{$t('base.TicketRestrictions')}}</p>
            </div>
            <p v-if="language === 'EN'" class="ticketRestrictions" @click="_getFlightCabinRule(cheapestCabinsListRecommend.FlightDetails, cheapestCabinsListRecommend.CabinDetails)">{{$t('base.TicketRestrictions')}}</p>
          </div>
        </div>
        <div class="buttons">
          <button @click="_bookTheLowestLogicalFare">{{$t('base.BookTheLowestLogicalFare')}}</button>
          <button @click="_gotoOverProof">{{$t('base.ContinuetoBook')}}</button>
        </div>
      </div>
    </hs-dialog>

    <!-- 去程详情 -->
    <hs-dialog v-model="showPreviousPlaneDialog">
      <div class="previousPlaneDialog" v-if="!isGoFlight">
        <div class="header">{{$t('base.FlightDetails')}}</div>
        <div class="details">
          <div class="header">
            <!-- <p>去</p> -->
            <div class="logo" :style="{'background-image': `url(${flightOrder.GoSelectedFlight.FlightDetails.AirLineLogoUrl})`}"></div>
            <p>
              <template v-if="flightOrder.GoSelectedFlight.FlightDetails.AirLineName">{{flightOrder.GoSelectedFlight.FlightDetails.AirLineName}}</template>
              <span v-if="flightOrder.GoSelectedFlight.FlightDetails.IsCodeShare">{{$t('base.CodeShare')}}</span>
              &nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.FlightNo}}
              <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneName">&nbsp;|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.PlaneName}}</template>
              <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneType">
                {{flightOrder.GoSelectedFlight.FlightDetails.PlaneType}}
              </template>
              &nbsp;|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}
              {{beTime(flightOrder.GoSelectedFlight.FlightDetails.DepartDateTime)}}
              <template v-if="flightOrder.GoSelectedFlight.FlightDetails.IsCodeShare">
                <br />
                <p>{{$t('base.OperatedBy')}}&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.ActualCarryAirLineName}}&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.ActualCarryFlightNo}}</p>
              </template>
            </p>
          </div>
          <div class="travel">
            <div class="source">
              <p>{{beTime(flightOrder.GoSelectedFlight.FlightDetails.DepartDateTime, 'hh:mm')}}</p>
              <p>{{flightOrder.GoSelectedFlight.FlightDetails.DepartAirportName}} {{flightOrder.GoSelectedFlight.FlightDetails.DepartTerminal}}</p>
            </div>
            <div class="middle">
              <div class="stopOver-logo" v-if="flightOrder.GoSelectedFlight.FlightDetails.StopOver">
                <p>{{$t('base.By')}}</p>
              </div>
              <div class="logo" v-else style="background-image: url('static/image/home/trip_plane.png')"></div>
              <div class="dashed"></div>
              <p class="stopOver" v-if="flightOrder.GoSelectedFlight.FlightDetails.StopOver">{{flightOrder.GoSelectedFlight.FlightDetails.StopOver.StopCity}}</p>
            </div>
            <div class="destination">
              <p>
                {{beTime(flightOrder.GoSelectedFlight.FlightDetails.ArrivalDateTime, 'hh:mm')}}
                <span v-if="flightOrder.GoSelectedFlight.FlightDetails.AddDays > 0">
                  <template v-if="language === 'CN'">+{{flightOrder.GoSelectedFlight.FlightDetails.AddDays}}天</template>
                  <template v-else>+{{flightOrder.GoSelectedFlight.FlightDetails.AddDays}}</template>
                </span>
              </p>
              <p>{{flightOrder.GoSelectedFlight.FlightDetails.ArrivalAirportName}} {{flightOrder.GoSelectedFlight.FlightDetails.ArrivalTerminal}}</p>
            </div>
          </div>
          <button @click="_reChoose">{{$t('base.Reselect')}}</button>
        </div>
      </div>
    </hs-dialog>

    <!-- 退改签 -->
    <c-return-explain v-model="showExplain" :details="returnExplainDetails"></c-return-explain>

    <!-- 差旅标准 -->
    <c-travel-standard v-model="showTravelStandard" :details="flightRight.FlightRank" :travelPolicy="flightRight.TravelPolicy"></c-travel-standard>

    <!-- 最低价提醒 -->
    <overproof v-model="showOverproof" :details="checkLeftSeatDetails" @delegate="_gotoOverProofDelegate"></overproof>

    <!-- 航班详情 -->
    <infor-list-details
      v-model="showInforListDetails"
      :details="inforListDetailsData"
      :isOneWay="isOneWay"
      :isGoFlight="isGoFlight"
      :flightFilters="details.FlightFilters"
      @clickBook="_clickBook"
      @clickReturnExplain="_clickReturnExplain"></infor-list-details>

    <hs-alert :show.sync="showDepartAlert" title="请务必确认" maskClose>
      <div class="departAlert" slot="content">
        <div class="details">
          <p>1. 购票前请先到值机柜台确认出票后仍有时间值机。</p>
          <p>2. 若已出票，退改损失需自行承担。</p>
        </div>
      </div>
      <div class="departAlert-footer" slot="footer">
        <p @click="showDepartAlert = false">值机柜台已关闭，暂不预订</p>
        <p @click="_delegateForExtend">已到柜台确认值机时间，可预订</p>
      </div>
    </hs-alert>
  </div>
</template>
<script type="text/ecmascript-6">
// 机票预订查询列表
import CDatePicker from 'components/c-date-picker/c-date-picker'
import CHeader from 'components/c-header/c-header'
import CReturnExplain from 'components/c-return-explain/c-return-explain'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsAlert from 'base/hs-alert/hs-alert'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDatePicker from 'base/hs-date-picker/hs-date-picker'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsFilterPlane from 'base/hs-filter/hs-filter-plane'
import HsNull from 'base/hs-null/hs-null'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import InforListCell from './infor-list-cell'
import InforListDetails from './infor-list-details'
import Overproof from './overproof'
import {mapGetters, mapMutations, mapActions} from 'vuex'
import {kpiFlightGetFlightList, kpiFlightFiltFlights, kpiFlightGetFlightCabinRule, kpiFlightCheckLeftSeat, kpiFlightGetFlightChangeList} from 'api'
export default {
  beforeRouteEnter(to, from, next) {
    console.log('from: ', from)
    next(vm => {
      if (from.path === '/' || from.path === '/planeTicket/query' || from.path === '/planeTicket/toReschedule') {
        vm.details = {}
        vm.dispatchFlightRight().then((res) => {
          vm._changeDateMore(vm.flightOrder.GoSelectedFlight.DepartDate)
        }) // 这么做是希望,只有查询页面过来,列表才请求新数据
      } else {
        vm.details = JSON.parse(JSON.stringify(vm.details))
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    if (this.$refs.recommendDialog && this.$refs.recommendDialog.showDetails === true) this.$refs.recommendDialog.showDetails = false
    let path = to.path
    if (this.showOverproof === true) {
      this.showOverproof = false
      next(false)
    } else if (this.showInforListDetails === true) {
      this.showInforListDetails = false
      next(false)
    } else if (path !== '/planeTicket/bookDetails' && path !== '/home' && !this.isOneWay && !this.isGoFlight) {
      this.$alert({
        content: this.$t('TM.YourTripHasNotBeenSelectedYet'),
        confirmFn: () => {
          this._clearGoAndBackFlight()
          next()
        },
        cancelFn: () => {
          next(false)
        }
      })
      next(false)
    } else if (path !== '/planeTicket/bookDetails' && this.isOneWay) {
      this._clearGoAndBackFlight()
      next()
    } else {
      // 并且关闭其他窗口
      this.showFilter = false
      this.showDatePickerDetails = false
      this.showTravelStandard = false
      this.showRecommendDialog = false
      this.showPreviousPlaneDialog = false
      this.showExplain = false
      this.showOverproof = false
      next()
    }
  },
  computed: {
    ...mapGetters([
      'flightOrder',
      'flightRight'
    ]),
    // 判断是不是单程
    isOneWay() {
      return `${this.$route.query.isOneWay}` === 'true'
    },
    // 是否为去程
    isGoFlight() {
      return this.noValue(this.flightOrder.GoSelectedFlight.FlightNo)
    },
    // 回程是否已经选择
    isFinishBackFlight() {
      return !this.noValue(this.flightOrder.BackSelectedFlight.FlightNo)
    },
    // 最低价提醒
    // enableFloorPricePrompt() {
    //   return this.details.EnableFloorPricePrompt
    // },
    // 是否为改期订单
    IsChangeOrder() {
      return !this.noValue(this.flightOrder.OriginPnrID)
    }
  },
  watch: {
    'details.FlightFilters': {
      handler: function(val) {
        this.hasFilters = false
        if (val) {
          let res = false
          val.forEach(element => {
            if (element.Items) {
              element.Items.forEach(elementChild => {
                if (elementChild.ItemValue !== '全部' && elementChild.IsChecked) {
                  res = true
                }
              })
            }
          })
          this.hasFilters = res
        }
      },
      deep: true
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        },
        pullUpLoad: false
      },
      currentPageIndex: 1,
      currentPageSize: 30,
      details: {},
      datalist: [],

      /** 排序参数 */
      Sort: undefined,
      /*******************/

      /** 舱位详情参数 */
      showInforListDetails: false,
      inforListDetailsData: {},
      /*******************/

      /** 检查剩余座位 */
      checkLeftSeatDetails: {},

      /** 继续预订原航班, 填写违反最低价原因 */
      showOverproof: false,

      /** 出发临近时间提醒 */
      showDepartAlert: false,

      // cheapestCabinsList: [],
      cheapestCabinsListRecommend: {},
      showFilter: false,
      showDatePickerDetails: false,
      showRecommendDialog: false,
      showPreviousPlaneDialog: false,
      showExplain: false,
      showTravelStandard: false,

      returnExplainDetails: {},
      waitingFlight: {}, // 点击预订按钮以后暂存的航班信息，不马上加入vuex
      waitingCabin: {}, // 点击预订按钮以后暂存的航班舱位信息，不马上加入vuex
      hasFilters: false // 是否有筛选条件
    }
  },
  methods: {
    ...mapMutations([
      'SET_FLIGHT_ORDER'
    ]),
    ...mapActions([
      'dispatchFlightRight'
    ]),

    /** 直接点击选择日期 */
    _setDate(val) {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      if (this.isGoFlight || this.isOneWay) {
        res.GoSelectedFlight.DepartDate = val
        res.GoLowestPriceFlights.forEach((element, index) => {
          res.GoLowestPriceFlights[index].DepartDate = val
        })
      } else {
        res.BackSelectedFlight.DepartDate = val
        res.BackLowestPriceFlights.forEach((element, index) => {
          res.BackLowestPriceFlights[index].DepartDate = val
        })
      }
      this.SET_FLIGHT_ORDER(res)
      this._getDatalistFirst()
    },

    /** 更多日期中选择日期 */
    _changeDateMore(val = ((this.isGoFlight || this.isOneWay) ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate)) {
      if (this.noValue(this.flightOrder.TravelType)) {
        this.$router.replace('/home')
        return
      }
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      if (this.isGoFlight || this.isOneWay) {
        res.GoSelectedFlight.DepartDate = val
        res.GoLowestPriceFlights.forEach((element, index) => {
          res.GoLowestPriceFlights[index].DepartDate = val
        })
      } else {
        res.BackSelectedFlight.DepartDate = val
        res.BackLowestPriceFlights.forEach((element, index) => {
          res.BackLowestPriceFlights[index].DepartDate = val
        })
      }
      this.SET_FLIGHT_ORDER(res)
      this.$refs.cDatePicker.setDateList(val)
      setTimeout(() => {
        let beforeVal = this.beTime(new Date(val).setDate(new Date(val).getDate() - 2))
        let beforeDom = document.getElementById(`cDatePicker${beforeVal}`)
        this.$refs.cDatePicker.scrollToElement(beforeDom ? beforeVal : val)
      }, 400)
    },

    /** 获取航班列表 不读缓存不带筛选 */
    _getDatalistFirst() {
      this.details = {}
      this.datalist = []
      this.Sort = undefined
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false

      // this.$refs.cDatePicker.setDateList(`${(this.isGoFlight || this.isOneWay) ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate}`)
      let params = this.noValue(this.flightOrder.OriginPnrID) ? {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        GoSelectedFlightArrivalDate: this.isGoFlight || this.isOneWay ? 0 : this.beTimeNumber(this.flightOrder.GoSelectedFlight.DepartDate),
        TravelType: this.flightOrder.TravelType,
        DepartCode: this.isGoFlight || this.isOneWay ? this.flightOrder.DepartCode : this.flightOrder.ArrivalCode,
        ArrivalCode: this.isGoFlight || this.isOneWay ? this.flightOrder.ArrivalCode : this.flightOrder.DepartCode,
        DepartCityName: this.isGoFlight || this.isOneWay ? this.flightOrder.DepartCityName : this.flightOrder.ArrivalCityName,
        ArrivalCityName: this.isGoFlight || this.isOneWay ? this.flightOrder.ArrivalCityName : this.flightOrder.DepartCityName,
        DepartCityCode: this.isGoFlight || this.isOneWay ? this.flightOrder.DepartCityCode : this.flightOrder.ArrivalCityCode,
        ArrivalCityCode: this.isGoFlight || this.isOneWay ? this.flightOrder.ArrivalCityCode : this.flightOrder.DepartCityCode,
        DepartGeoType: this.isGoFlight || this.isOneWay ? this.flightOrder.DepartGeoType : this.flightOrder.ArrivalGeoType,
        ArrivalGeoType: this.isGoFlight || this.isOneWay ? this.flightOrder.ArrivalGeoType : this.flightOrder.DepartGeoType,
        DepartDate: this.beTimeNumber(this.isGoFlight || this.isOneWay ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate),
        FlightClass: this.$route.query.FlightClass
      } : {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        ItktID: this.flightOrder.OriginBookingID,
        PnrID: this.flightOrder.OriginPnrID,
        FlightClass: 0,
        DepartDate: this.beTimeNumber(this.isGoFlight || this.isOneWay ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate),
        SegmentID: this.isGoFlight || this.isOneWay ? this.flightOrder.SegmentIDList[0] : this.flightOrder.SegmentIDList[1],
        GoSelectedFlightArrivalDate: this.isGoFlight || this.isOneWay ? 0 : this.beTimeNumber(this.flightOrder.GoSelectedFlight.DepartDate)
      }
      console.log('航班列表params: ', params)
      if (this.noValue(this.flightOrder.OriginPnrID)) {
        kpiFlightGetFlightList(params).then((res) => {
          console.log('航班列表First: ', res.ResultData)
          this.details = res.ResultData
          this.datalist = res.ResultData.Flights
          if (res.ResultData.Flights.length >= this.currentPageSize) {
            this.tableViewOptions.pullUpLoad = {threshold: 50}
          }
          this.$refs.tableView && this.$refs.tableView.forceUpdate()
        }).catch(() => {
          this.$refs.tableView.forceUpdate()
        })
      } else {
        kpiFlightGetFlightChangeList(params).then((res) => {
          this.details = res.ResultData
          this.datalist = res.ResultData.Flights
          if (res.ResultData.Flights.length >= this.currentPageSize) {
            this.tableViewOptions.pullUpLoad = {threshold: 50}
          }
          this.$refs.tableView && this.$refs.tableView.forceUpdate()
        }).catch(() => {
          this.$refs.tableView.forceUpdate()
        })
      }
    },
    /** 获取航班列表 读缓存读筛选 */
    _getDatalist() {
      this.datalist = []
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        DepartCode: this.isGoFlight || this.isOneWay ? this.flightOrder.DepartCode : this.flightOrder.ArrivalCode,
        ArrivalCode: this.isGoFlight || this.isOneWay ? this.flightOrder.ArrivalCode : this.flightOrder.DepartCode,
        DepartDate: this.beTimeNumber(this.isGoFlight || this.isOneWay ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate),
        Sort: this.Sort,
        SelectedFilters: this.details.FlightFilters.map(v => {
          return {
            FilterType: v.FilterType,
            SelectedValues: v.Items.filter(a => a.IsChecked).map(b => b.ItemValue)
          }
        }),
        IsChangeOrder: this.IsChangeOrder
      }
      kpiFlightFiltFlights(params).then((res) => {
        console.log('筛选国际航班列表: ', res)
        this.datalist = res.ResultData.Flights
        if (res.ResultData.Flights.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    /** 获取更多航班列表 读缓存读筛选 */
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        DepartCode: this.isGoFlight || this.isOneWay ? this.flightOrder.DepartCode : this.flightOrder.ArrivalCode,
        ArrivalCode: this.isGoFlight || this.isOneWay ? this.flightOrder.ArrivalCode : this.flightOrder.DepartCode,
        DepartDate: this.beTimeNumber(this.isGoFlight || this.isOneWay ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate),
        Sort: this.Sort,
        SelectedFilters: this.details.FlightFilters.map(v => {
          return {
            FilterType: v.FilterType,
            SelectedValues: v.Items.filter(a => a.IsChecked).map(b => b.ItemValue)
          }
        }),
        IsChangeOrder: this.IsChangeOrder
      }
      kpiFlightFiltFlights(params).then((res) => {
        console.log('筛选国际航班列表更多: ', res)
        this.datalist = this.datalist.concat(res.ResultData.Flights)
        if (res.ResultData.Flights.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },

    /** 筛选条件 */
    _setFlightFilters(val) {
      console.log('筛选: ', val)
      this.details.FlightFilters = val
      this._getDatalist()
    },

    /** 显示退改签 */
    _clickReturnExplain(res) {
      this.showExplain = true
      this.returnExplainDetails = res
    },

    /** 预订 */
    _clickBook(flight, cabin) {
      this.cheapestCabinsListRecommend = {} // 重置

      this._clickBookWithTime(flight, cabin)
    },

    /** 点击预订流程_1_校验间隔时间 */
    _clickBookWithTime(flight, cabin) {
      // TODO
      this._clickBookWithLeftSeat(flight, cabin)
    },

    /** 点击预订流程_2_校验剩余座位&是否可预订&是否违反最低价 */
    _clickBookWithLeftSeat(flight, cabin) {
      let params = {
        DepartAirportCode: flight.DepartAirportCode,
        ArrivalAirportCode: flight.ArrivalAirportCode,
        DepartCode: (this.isGoFlight || this.isOneWay) ? this.flightOrder.DepartCode : this.flightOrder.ArrivalCode,
        ArrivalCode: (this.isGoFlight || this.isOneWay) ? this.flightOrder.ArrivalCode : this.flightOrder.DepartCode,
        GoSelectedFlight: {
          FlightNo: flight.FlightNo,
          CabinID: cabin.CabinID,
          DepartDate: this.beTimeNumber(this.isGoFlight || this.isOneWay ? this.flightOrder.GoSelectedFlight.DepartDate : this.flightOrder.BackSelectedFlight.DepartDate)
        },
        TravelType: this.flightOrder.TravelType,
        IsCodeShare: flight.IsCodeShare,
        ActualCarryFlightNo: flight.ActualCarryFlightNo,
        IsChangeOrder: this.IsChangeOrder
      }
      kpiFlightCheckLeftSeat(params).then((res) => {
        console.log('校验剩余座位: ', res)
        this.checkLeftSeatDetails = res.ResultData
        // 1.有座位
        if (this.checkLeftSeatDetails.IsHaveLeftSeat === true) {
          // 2.可否预订
          if (this.checkLeftSeatDetails.IsBookable === false) {
            this.$alert({
              content: this.checkLeftSeatDetails.Message,
              confirmTxt: this.$t('base.RefreshFlight'),
              cancelTxt: this.$t('base.ReElection'),
              confirmFn: () => {
                this.showInforListDetails = false
                this._getDatalistFirst()
              }
            })
          } else {
            // 3.是否违反最低价
            if (this.checkLeftSeatDetails.IsViolateLowestPrice === true) {
              this.cheapestCabinsListRecommend = {
                FlightNo: this.checkLeftSeatDetails.RecommendFlightInfo.FlightNo,
                CabinID: this.checkLeftSeatDetails.RecommendFlightInfo.Cabins[0].CabinID,
                DepartDate: this.checkLeftSeatDetails.RecommendFlightInfo.DepartDateTime,
                FlightDetails: this.checkLeftSeatDetails.RecommendFlightInfo,
                CabinDetails: this.checkLeftSeatDetails.RecommendFlightInfo.Cabins[0]
              }
              this.showRecommendDialog = true
              this.waitingFlight = flight
              this.waitingCabin = cabin
            } else {
              // TODO: 直接选择最低价
              console.log('flight, cabin: ', flight, cabin)
              if (this.isOneWay || (!this.isOneWay && this.isGoFlight)) { // 如果是去程，就保存去程的信息
                let res = JSON.parse(JSON.stringify(this.flightOrder))
                res.GoSelectedFlight = {
                  FlightNo: flight.FlightNo,
                  CabinID: cabin.CabinID,
                  DepartDate: this.beTime(flight.DepartDateTime),
                  FlightDetails: flight,
                  CabinDetails: cabin
                }
                this.SET_FLIGHT_ORDER(res)
              } else {
                let res = JSON.parse(JSON.stringify(this.flightOrder))
                res.BackSelectedFlight = {
                  FlightNo: flight.FlightNo,
                  CabinID: cabin.CabinID,
                  DepartDate: this.beTime(flight.DepartDateTime),
                  FlightDetails: flight,
                  CabinDetails: cabin
                }
                this.SET_FLIGHT_ORDER(res)
              }
              this.showInforListDetails = false
              if (!this.isOneWay && !this.isFinishBackFlight) {
                this._changeDateMore()
              } else {
                this.$router.push({path: '/planeTicket/bookDetails'})
              }
            }
          }
        } else {
          this.$alert({
            content: this.$t('TM.TheFlightHasInsufficientTickets'),
            confirmTxt: this.$t('base.RefreshFlight'),
            confirmFn: () => {
              this.showInforListDetails = false
              this._getDatalistFirst()
            }
          })
        }
      })
    },

    /** 预订最低价航班 */
    _bookTheLowestLogicalFare() {
      this.$alert({
        content: '请确认是否将您的航班变更为最低价航班?',
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          if (this.isOneWay || (!this.isOneWay && this.isGoFlight)) { // 如果是去程，就保存去程的信息
            let res = JSON.parse(JSON.stringify(this.flightOrder))
            res.GoSelectedFlight = {
              FlightNo: this.cheapestCabinsListRecommend.FlightNo,
              CabinID: this.cheapestCabinsListRecommend.CabinID,
              DepartDate: this.beTime(this.cheapestCabinsListRecommend.DepartDate),
              FlightDetails: this.cheapestCabinsListRecommend.FlightDetails,
              CabinDetails: this.cheapestCabinsListRecommend.CabinDetails
            }
            this.SET_FLIGHT_ORDER(res)
          } else {
            let res = JSON.parse(JSON.stringify(this.flightOrder))
            res.BackSelectedFlight = {
              FlightNo: this.cheapestCabinsListRecommend.FlightNo,
              CabinID: this.cheapestCabinsListRecommend.CabinID,
              DepartDate: this.beTime(this.cheapestCabinsListRecommend.DepartDate),
              FlightDetails: this.cheapestCabinsListRecommend.FlightDetails,
              CabinDetails: this.cheapestCabinsListRecommend.CabinDetails
            }
            this.SET_FLIGHT_ORDER(res)
          }
          console.log('this.flightOrder: ', this.flightOrder)
          this.showInforListDetails = false
          if (!this.isOneWay && !this.isFinishBackFlight) {
            if (this.$refs.recommendDialog && this.$refs.recommendDialog.showDetails === true) {
              this.$refs.recommendDialog.showDetails = false
            }
            this._changeDateMore()
          } else {
            this.$router.push({path: '/planeTicket/bookDetails'})
          }
        }
      })
    },

    /** 最低价航班获取退改签信息 */
    _getFlightCabinRule(flight, cabin) {
      let params = {
        DepartCode: flight.DepartCityCode,
        ArrivalCode: flight.ArrivalCityCode,
        FlightNo: flight.FlightNo,
        CabinID: cabin.CabinID,
        DepartDate: flight.DepartDateTime,
        IsChangeOrder: this.IsChangeOrder
      }
      console.log('退改签params： ', params)
      kpiFlightGetFlightCabinRule(params).then((res) => {
        console.log('退改签政策： ', res)
        this._clickReturnExplain(res.ResultData)
      })
    },

    /** 继续预订原航班,去填写超标原因 */
    _gotoOverProof() {
      if (this.$refs.recommendDialog && this.$refs.recommendDialog.showDetails === true) {
        this.$refs.recommendDialog.showDetails = false
      }
      this.showOverproof = true
    },

    /** 填写违反最低价原因回调 */
    _gotoOverProofDelegate(ViolationFloorPirceReason) {
      if (this.isOneWay || (!this.isOneWay && this.isGoFlight)) { // 如果是去程，就保存去程的信息
        let res = JSON.parse(JSON.stringify(this.flightOrder))
        res.GoSelectedFlight = {
          FlightNo: this.waitingFlight.FlightNo,
          CabinID: this.waitingCabin.CabinID,
          DepartDate: this.flightOrder.GoSelectedFlight.DepartDate,
          FlightDetails: this.waitingFlight,
          CabinDetails: this.waitingCabin
        }
        res.GoLowestPriceFlights = res.GoLowestPriceFlights.map(v => {
          v.ViolationFloorPirceReason = ViolationFloorPirceReason
          v.FloorPriceTimeRange = this.checkLeftSeatDetails.FloorTimeRange
          v.openLowestPrice = false
          return v
        }) // 违反最低价原因
        this.SET_FLIGHT_ORDER(res)
      } else {
        let res = JSON.parse(JSON.stringify(this.flightOrder))
        res.BackSelectedFlight = {
          FlightNo: this.waitingFlight.FlightNo,
          CabinID: this.waitingCabin.CabinID,
          DepartDate: this.flightOrder.BackSelectedFlight.DepartDate,
          FlightDetails: this.waitingFlight,
          CabinDetails: this.waitingCabin
        }
        res.BackLowestPriceFlights = res.BackLowestPriceFlights.map(v => {
          v.ViolationFloorPirceReason = ViolationFloorPirceReason
          v.FloorPriceTimeRange = this.checkLeftSeatDetails.FloorTimeRange
          v.openLowestPrice = false
          return v
        }) // 违反最低价原因
        this.SET_FLIGHT_ORDER(res)
      }
      this.showInforListDetails = false
      this.showOverproof = false
      if (!this.isOneWay && !this.isFinishBackFlight) {
        this._changeDateMore()
      } else {
        this.$router.push({path: '/planeTicket/bookDetails'})
      }
    },

    // 重新选择
    _reChoose() {
      this._clearGoAndBackFlight()
      this._changeDateMore(this.flightOrder.GoSelectedFlight.DepartDate)
    },

    /** 校验出发剩余时间 */
    _checkDepartTime(item) {
      this.inforListDetailsData = JSON.parse(JSON.stringify(item))
      if (((item.DepartDateTime * 1000) - Number(new Date())) > (2 * 60 * 60 * 1000)) {
        this._delegateForExtend()
      } else {
        this.showDepartAlert = true
      }
    },
    /** 展开舱位 */
    _delegateForExtend() {
      this.showDepartAlert = false
      this.showInforListDetails = true
    },
    // 清除往返程vuex信息
    _clearGoAndBackFlight() {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res.GoSelectedFlight.FlightNo = ''
      res.GoSelectedFlight.CabinID = ''
      res.GoSelectedFlight.FlightDetails = {}
      res.GoSelectedFlight.CabinDetails = {}
      res.BackSelectedFlight.FlightNo = ''
      res.BackSelectedFlight.CabinID = ''
      res.BackSelectedFlight.FlightDetails = {}
      res.BackSelectedFlight.CabinDetails = {}
      res.GoLowestPriceFlights = []
      res.BackLowestPriceFlights = []
      this.SET_FLIGHT_ORDER(res)
      // 并且关闭其他窗口
      this.showFilter = false
      this.showDatePickerDetails = false
      this.showTravelStandard = false
      this.showRecommendDialog = false
      this.showPreviousPlaneDialog = false
      this.showExplain = false
      this.showOverproof = false
    },
    _scrollStart() {
      this.$refs.footer && (this.$refs.footer.style.bottom = '-50px')
    },
    _scroll(pos) {
      if (!this.$refs['previous-plane-fixed']) return
      if (pos.y < -45) {
        this.$refs['previous-plane-fixed'].style.opacity = 1
        this.$refs['previous-plane-fixed'].style['pointer-events'] = 'auto'
      } else {
        this.$refs['previous-plane-fixed'].style.opacity = 0
        this.$refs['previous-plane-fixed'].style['pointer-events'] = 'none'
      }
    },
    _touchEnd(pos) {
      // console.log(pos.y)
      this.$refs.footer && (this.$refs.footer.style.bottom = '0px')
    }
  },
  components: {
    CDatePicker,
    CHeader,
    CReturnExplain,
    CTravelStandard,
    HsAlert,
    HsCell1,
    HsDatePicker,
    HsDialog,
    HsFilterPlane,
    HsNull,
    HsScroll,
    HsSection,
    InforListCell,
    InforListDetails,
    Overproof
  }
}
</script>
