          border-top 1px solid $color_gray_layer
        &:nth-child(2)
          background-color $color_theme
          color $color_white

  // 去程详情
  .previousPlaneDialog
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 100%
    padding 0 15px
    >.header
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 40px
      font-size 1rem
      color $color_white
    >.details
      @extend .flex-column
      width 100%
      padding 15px 10px
      background-color $color_white
      border-radius 2px
      >.header
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        width 100%
        line-height 1.2em
        >p
          // &:nth-child(1)
          //   font-size 0.6rem
          //   color $color_white
          //   background-color $color_theme
          //   padding 2px 3px
          //   border-radius 3px
          //   margin-right 5px
          &:nth-child(2)
            font-size 0.8rem
            width calc(100% - 20px)
            >span
              color $color_theme
        >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin-right 5px
            margin-top 2px
      >.travel
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-top 15px
        >.source
          @extend .flex-column
          @extend .flex-center
          width 90px
          >p
            &:nth-child(1)
              font-size 1.5rem
              font-weight bold
              line-height 1.2em
            &:nth-child(2)
              font-size 0.8rem
              line-height 1.4em
              text-align center
        >.middle
          @extend .flex-center
          width calc(100% - 180px)
          position relative
          .dashed
            position absolute
            width 100%
            height 0px
            border-top 1px dashed $color_black_text_0
            top 50%
            left 0
          .logo
            width 15px
            height 15px
            background-size 100% 100%
            background-color $color_white
          >.stopOver
            position absolute
            top 18px
            left 0
            font-size 0.6rem
            color $color_theme
            width 100%
            text-align center
          >.stopOver-logo
            @extend .flex-center
            width 24px
            height 18px
            background-color $color_white
            z-index 9
            >p
              text-align center
              width 18px
              line-height 1.2em
              color $color_theme
              font-size 0.6rem
        >.destination
          @extend .flex-column
          @extend .flex-center
          width 90px
          >p
            &:nth-child(1)
              font-size 1.5rem
              font-weight bold
              line-height 1.2em
              position relative
              >span
                color $color_theme
                font-size 0.7rem
                position absolute
                top -1rem
                right 0px
            &:nth-child(2)
              font-size 0.8rem
              line-height 1.4em
              text-align center
      >button
        width 100%
        padding 10px 0
        background-color $color_theme
        color $color_white
        border-radius 4px
        margin-top 20px
        font-size 1rem
  >.footer
    @extend .flex-row
    @extend .flex-between
    transition all 300ms
    position absolute
    bottom 0
    left 0
    width 100%
    height 50px
    background-color $color-black_1
    >.item
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      height 100%
      font-size 0.8rem
      color $color_white
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-bottom 5px
        position relative
        >.dot
          position absolute
          top -1px
          right 0
          background-color $color_theme
          width 6px
          height 6px
          border-radius 3px
  >.footer[type="EN"]
    >.item
      font-size 0.7rem

  // 请务必确认
  .departAlert
    width 100%
    padding 0 20px
    >.details
      width 100%
      >p
        line-height 1.5em
        text-align justify
        color $color_black_text_0
  .departAlert-footer
    width 100%
    padding 10px 20px
    >p
      width 100%
      padding 10px
      color $color_blue_text
      font-size 0.9rem
      text-align center
      border 1px solid $color_blue_text
      border-radius 3px
      line-height 1.2em
      margin-bottom 10px
</style>



