<template>
  <div class="plane-ticket-book-infor-list-cell">
    <div class="cell" @click="_switchExtend">
      <div class="travel-details" :style="{'border-bottom-width': extend ? '0px' : '1px'}">
        <div class="left">
          <div class="travel">
            <div class="source">
              <p>{{beTime(currentValue.DepartDateTime, 'hh:mm')}}</p>
              <p>{{currentValue.DepartAirportAbbrName}} {{currentValue.DepartTerminal}}</p>
            </div>
            <div class="middle">
              <div class="stopOver-logo" v-if="currentValue.StopOver">
                <p>{{$t('base.By')}}</p>
              </div>
              <div class="logo" v-else style="background-image: url('static/image/home/trip_plane.png')"></div>
              <div class="dashed"></div>
              <p class="stopOver" v-if="currentValue.StopOver">{{currentValue.StopOver.StopCity}}</p>
            </div>
            <div class="destination">
              <p>
                {{beTime(currentValue.ArrivalDateTime, 'hh:mm')}}
                <span v-if="currentValue.AddDays > 0">
                  <template v-if="language === 'CN'">+{{currentValue.AddDays}}天</template>
                  <template v-else>+{{currentValue.AddDays}}</template>
                </span>
              </p>
              <p>{{currentValue.ArrivalAirportAbbrName}} {{currentValue.ArrivalTerminal}}</p>
            </div>
          </div>
          <div class="company">
            <div class="logo" :style="{'background-image': `url(${currentValue.AirLineLogoUrl})`}"></div>
            <div class="infor">
              <p>
                {{currentValue.AirLineName}}
                <span v-if="currentValue.IsCodeShare">{{$t('base.CodeShare')}}</span>
                {{currentValue.FlightNo}}
                |&nbsp;{{currentValue.PlaneName}}
              </p>
              <p v-if="currentValue.IsCodeShare">
                {{$t('base.OperatedBy')}}
                {{currentValue.ActualCarryAirLineName}}
                {{currentValue.ActualCarryFlightNo}}
              </p>
            </div>
          </div>
        </div>
        <div class="right">
          <p class="price">￥{{currentValue.CabinPrice}}</p>
          <p v-show="currentValue.HasAgreementPrice">{{$t('base.HaveAgreement')}}</p>
        </div>
        <p class="minPrice" v-if="currentValue.IsLowestPrice">{{$t('base.LowestPrice')}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import {mapGetters} from 'vuex'
// 机票预订查询列表CELL
export default {
  props: {
    value: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    ...mapGetters([
      'flightOrder'
    ]),
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    },
    // 是否为改期订单
    IsChangeOrder() {
      return !this.noValue(this.flightOrder.OriginPnrID)
    }
  },
  data() {
    return {
      extend: false
    }
  },
  methods: {
    _switchExtend() {
      console.log(this.currentValue)
      this.$emit('delegateForExtend', this.currentValue)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.plane-ticket-book-infor-list-cell
  width 100%
  >.cell
    width 100%
    padding 0 10px
    background-color $color_gray_background_0
    position relative
    >.travel-details
      @extend .flex-row
      @extend .flex-between
      @extend .flex-top
      width 100%
      padding 20px 10px
      background-color $color_white
      border-radius 5px
      box-shadow 0 0 10px $color_gray_boxshadow
      margin-bottom 10px
      >.left
        width calc(100% - 85px)
        >.travel
          @extend .flex-row
          @extend .flex-between
          width 100%
          >.source
            @extend .flex-column
            @extend .flex-center
            width 80px
            >p
              &:nth-child(1)
                font-size 1.5rem
                font-weight bold
                line-height 1.2em
              &:nth-child(2)
                font-size 0.8rem
                line-height 1.4em
                text-align center
                margin-top 3px
          >.middle
            @extend .flex-center
            width calc(100% - 170px)
            position relative
            .dashed
              position absolute
              width 100%
              height 0px
              border-top 1px dashed $color_black_text_0
              top 50%
              left 0
            .logo
              width 15px
              height 15px
              background-size 100% 100%
              background-color $color_white
            >.stopOver
              position absolute
              top 18px
              left 0
              font-size 0.6rem
              color $color_theme
              width 100%
              text-align center
            >.stopOver-logo
              @extend .flex-center
              width 24px
              height 18px
              background-color $color_white
              z-index 9
              >p
                text-align center
                width 18px
                line-height 1.2em
                color $color_theme
                font-size 0.6rem
          >.destination
            @extend .flex-column
            @extend .flex-center
            width 80px
            >p
              &:nth-child(1)
                font-size 1.5rem
                font-weight bold
                line-height 1.2em
                position relative
                >span
                  color $color_theme
                  font-size 0.7rem
                  position absolute
                  top -1rem
                  right 0px
              &:nth-child(2)
                font-size 0.8rem
                line-height 1.4em
                text-align center
                margin-top 3px
        >.company
          @extend .flex-row
          @extend .flex-start
          @extend .flex-top
          width 100%
          font-size 0.7rem
          color $color_black_text_0
          margin-top 13px
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin-right 3px
          >.infor
            width calc(100% - 18px)
            >p
              color $color_gray_text
              line-height 1.3em
              width calc(100% - 15px - 3px)
              >span
                color $color_theme
      >.right
        @extend .flex-column
        @extend .flex-start
        @extend .flex-bottom
        width 75px
        >p
          font-size 0.8rem
          line-height 1.4em
          color $color_gray_text
        >.price
          font-size 1.3rem
          font-weight bold
          color $color_theme
      >.minPrice
        position absolute
        top 0
        left 30px
        background-color $color_green_background_1
        color $color_green_text !important
        font-size 0.6rem
        padding 3px
        border-radius 2px
</style>



