<template>
  <div class="c-date-picker">
    <hs-scroll ref="cDatePicker" :data="datelistShow" class="table-view" direction="horizontal">
      <div style="min-height: 0px">
        <ul class="list-wrapper">
          <li
            class="date-cell"
            v-for="(item, index) in datelistShow" :key="index" :id="`cDatePicker${item.date}`"
            :class="{'date-cell-active': item.date === currentDate, 'disabledItem': _isOverdue(item.date)}"
            @click.prevent="_setCurrentDate(item.date)">
            <p>{{item.dateShow}}</p>
            <p>{{item.week}}</p>
          </li>
        </ul>
      </div>
    </hs-scroll>
    <div class="more" @click="$emit('moreFn')"><p v-html="$t('base.More')"></p></div>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  props: {
    date: {
      type: String,
      default: Vue.prototype.beTime(new Date(), 'yyyy-MM-dd')
    },
    overdueDate: {
      type: String,
      default: Vue.prototype.beTime(new Date())
    }
  },
  components: {
    HsScroll
  },
  mounted() {
    this._initDateList()
    this.datelistShow = JSON.parse(JSON.stringify(this.datelist))
  },
  computed: {
    currentDate: {
      get: function() {
        return this.date
      },
      set: function(val) {
        return this.$emit('changeDate', val)
      }
    }
  },
  data() {
    return {
      stopDoubleClick: false,
      datelist: [], // 最初的15个日期数组
      datelistShow: [] // 最终展示的变动的数组
    }
  },
  methods: {
    _initDateList() {
      let today = new Date()
      for (let i = 0; i < 15; i++) {
        let dateShow = this.$t(this.beTime(today), 'date', ['MM-dd', 'MM dd']) // 显示日期
        let date = this.beTime(today, 'yyyy-MM-dd') // 日期
        let day = today.getDate() // 日
        // let week = today.getDay() // 星期
        this.datelist.push({
          date: date,
          dateShow: dateShow,
          // week: week === 0 ? '周日' : (week === 1 ? '周一' : (week === 2 ? '周二' : (week === 3 ? '周三' : (week === 4 ? '周四' : (week === 5 ? '周五' : '周六')))))
          week: this.getWeeks(date)
        })
        today.setDate(day + 1)
      }
    },
    setDateList(val) {
      this.currentDate = val
      let datelistDate = Array.from(this.datelist, v => v.date)
      let datelistShowDate = Array.from(this.datelistShow, v => v.date)
      let index = datelistDate.indexOf(val)
      let indexShow = datelistShowDate.indexOf(val)
      if (index >= 0) {
        this.datelistShow = JSON.parse(JSON.stringify(this.datelist))
      } else if (index < 0 && indexShow < 0) { // 如果选择更多日期在最近的15天以外的, 就生成新的展示日期数组, 个数为7天
        this.datelistShow = []
        let valList = val.split('-')
        let startDate = new Date(new Date().setFullYear(valList[0], valList[1] - 1, valList[2] - 3))
        for (let i = 0; i < 7; i++) {
          let dateShow = this.beTime(startDate, 'MM-dd') // 显示日期
          let date = this.beTime(startDate, 'yyyy-MM-dd') // 日期
          let day = startDate.getDate() // 日
          // let week = startDate.getDay() // 星期
          this.datelistShow.push({
            date: date,
            dateShow: dateShow,
            // week: week === 0 ? '周日' : (week === 1 ? '周一' : (week === 2 ? '周二' : (week === 3 ? '周三' : (week === 4 ? '周四' : (week === 5 ? '周五' : '周六')))))
            week: this.getWeeks(date)
          })
          startDate.setDate(day + 1)
        }
      }
    },
    _setCurrentDate(date) {
      if (this.stopDoubleClick) {
        return
      } else {
        this.stopDoubleClick = true
        setTimeout(() => {
          this.stopDoubleClick = false
        }, 0)
      }
      this.currentDate = date
    },
    scrollToElement(date) {
      this.$refs.cDatePicker.scrollToElement(`#cDatePicker${date}`, 300)
    },
    // 日期是否已经过期
    _isOverdue(date) {
      // console.log(date, this.overdueDate)
      return this.overdueDate > date
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.c-date-picker
  @extend .flex-row
  @extend .flex-start
  width 100%
  height 45px
  position relative
  transform rotate(0deg)
  >.table-view
    @extend .flex-row
    @extend .flex-start
    width calc(100% - 40px)
    height 100%
    overflow hidden
    background-color $color_white
    .list-wrapper
      // @extend .flex-row
      // @extend .flex-start
      white-space nowrap
      display inline-block
      .date-cell
        display inline-block
        text-align center
        height 40px
        width 50px
        margin-right 10px
        font-size 0.7rem
        line-height 1.2em
        border-radius 3px
        padding 5px 0
        >p
          line-height 15px
      .date-cell-active
        background-color $color_theme
        color $color_white
      .disabledItem
        color $color_gray_text !important
        pointer-events none !important
  >.more
    @extend .flex-center
    width 40px
    height 100%
    font-size 0.8rem
    // letter-spacing 2px
    line-height 1.2em
.cube-scroll-content
  display inline-block
</style>



